@charset 'utf-8';
@import 'base/global.scss';

/* 
 * loyout.scss 
 * 框架内元素, 比如按钮, 编辑框等
*/

// 按钮
.btn {
  @extend %ib;
  @extend %ellipsis;
  text-align: center;
  padding: 0px 2em;
  height: 2.3em;
  line-height: 2.3em;
  border-radius: 0.3em;
  color: #fff;
  border: 1px solid $Theme-color;
  background-color: $Theme-color;
  font-size: 1em;
  font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  cursor: pointer;
  -webkit-appearance: none;
  user-select: none;

  &:hover {
    color: #fff;
    background-color: $Theme-color-light;
  }
  &.disabled {
    pointer-events: none;
    cursor: not-allowed;
    background-color: gainsboro;
    border: 1px solid gray;
    color: gray;
  }
  &.loading {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.7;
    filter: alpha(opacity=70);
  }

}

// 另一种按钮
.btn-medium {
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
}

// 按钮配色-1
.btn-primary {
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
}

// 小型按钮尺寸
.btn-small {
  @extend .btn;
  font-size: 0.9em;
  padding: 0px 1.5em;
}

// 表单内按钮右侧
.input-btn-right {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

// 标题
.title-h1 {
  padding: 0 0.8em;
  margin-bottom: 0.8em;
  line-height: 3.5em;
  color: #333;
  background: #f9f9f9;
  border-bottom: 1px solid #e4e4e4;

  .primary {
    font-size: 1.5em;
    color: black;
  }

}

// 子标题
.title-h2 {
  padding: 0 0.8em;
  margin-bottom: 0.8em;
  line-height: 2.3em;
  color: #333;
  background: #f9f9f9;
  border-bottom: 1px solid #e4e4e4;

  .primary {
    font-size: 1.1em;
    color: black;
  }
}

.title-h1.noBottom,
.title-h2.noBottom {
  margin-bottom: 0;
  border-bottom: none;
}


// 加载等待图片
.loading-img {
  @extend %full-size;
  display: none;
  background: transparent;
  z-index: 10;
  // filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#19000000,endColorstr=#19000000);

  .img {
    @extend %full-size;
    background: url($images-path + 'loading.gif') center center no-repeat;
  }
}

// 上传文件
.file-wrap {
  @extend %ib;
  position: relative;
  min-width: 100px;

  input[type="file"] {
    @extend %full-size;
    z-index: 2;
    opacity: 0.01;
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=1);
    cursor: pointer;
  }

  .btn-file {
    position: absolute;
    top: 0;
    right: 0;
  }



}



/* 表单布局 */
.query-box {
  padding: 0.8em;
}

// 通用表单
.item {
  margin-bottom: 0.8em;
  position: relative;
  .input-text,
  .input-select {
    width: 170px;
  }
  .double-input {
    width: 82px;
  }



  .label {
    min-width: 95px;
    text-align: right;
    margin-right: 0.8em;
    vertical-align: middle;
  }

}
.input-area {
  position: relative;
  .cut {
    vertical-align: middle;
  }
}
.label,
.input-wrap {
  position: relative;
  @extend %ib;
}
.item-row .item{
  float: left;
}



// 自适应表单
.adaptive {
  text-align: left;
  .item {
    @extend %ib;
    float: left;
    .input-text,
    .input-select {
      width: 170px;
    }
  }
  .label {
    @extend %ib;
    // @extend %ellipsis;
    width: 20%;
    text-align: right;
    margin-right: 0.8em;
    vertical-align: middle;
  }
}

[data-dpr="2"] {
  .item .label.w-90 {
    width: 95px;
  }
}


