/* 横屏 @media only screen and (orientation: landscape) */


@media only screen and (max-width: 550px) {
  .adaptive .item .input-text,
  .adaptive .item .input-select {
    width: 170px;
  }
  .adaptive .item .double-input {
    width: 100px;
  }
}


/* 小于于等于宽度 */
@media only screen and (max-width: 840px) {
  .adaptive .item {
    width: 100%;
  }
  .adaptive .item .label {
    width: 90px;
  }
}

/* 大于等于宽度 */
@media only screen and (min-width: 841px) {
  .adaptive .item {
    width: 50%;
  }
  .adaptive .item .label {
    width: 23%;
  }
  .adaptive .item .double-input {
    width: 110px;
  }
}

/* 大于等于宽度 */
@media only screen and (min-width: 1240px) {
  .adaptive .item {
    width: 33.33%;
  }
  .adaptive .item .label {
    width: 25%;
  }

  // 处理 6 Plus
  [data-dpr="3"] {
    .adaptive .item {
      width: 100%;
    }
  }


}
/* 大于等于宽度 */
@media only screen and (min-width: 1680px) {
  .adaptive .item {
    width: 25%; 
  }
  .adaptive .item .label {
    width: 25%;
  }
}

@media only screen and (min-width: 1240px) {
  .adaptive .item .input-text,
  .adaptive .item .input-select {
    width: 170px;
  }
  .adaptive .item .double-input {
    width: 135px;
  }
}












/* 查询结果框架 */
.query-result {
  position: relative;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  width: 100%;
  height: 500px;

  iframe {
    width: 100%;
    height: 100%;
  }
}

.table-warp {
  width: 100%;
  min-width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}


